import React, { Component } from 'react';
import RaisedButton from 'material-ui/RaisedButton';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import {TimelineMax} from 'gsap';
import GSAP from 'react-gsap-enhancer';

import styles from './MainButton.scss'

var DURATION = 0.05;

function pressDown({target}) {
  return new TimelineMax().to(target.find({name: 'button'}), DURATION, {
    left: 0,
    top: 0,
    ease: 'Power3.easeIn'
  });
}

function pressUp({target, options}) {
  var direction = 0;
  if (options.position === 'left') { direction = '-'+styles.buttonDepth; }
  else if (options.position === 'right') {direction = styles.buttonDepth; }

  return new TimelineMax().to(target.find({name: 'button'}), DURATION, {
    left: direction,
    top: '-'+styles.buttonDepth,
    ease: 'Power3.easeIn'
  });
}

function splitApart({target}) {
  return new TimelineMax({paused: true})
    .add('together')
    .to(target, 0.5, { top: '101vh', opacity: 0, ease: 'Power1.easeInOut' }, 'together')
    .add('split')
}

class MainButton extends Component {
  constructor(props) {
    super(props);

    this.buttonClasses = "MainButton " + props.position;
  }

  componentDidMount() {
    this.hideAnimation = this.addAnimation(splitApart);

  }

  componentWillReceiveProps(nextProps) {
    var parentPosition = nextProps.parentPosition;
    if (parentPosition === 'center') {
      this.hideAnimation.tweenTo('together', {delay: 0.8});
    } else {
      this.hideAnimation.tweenTo('split', {delay: 0.5});
    }

  }

  handleClick = () => {
    //this.addAnimation(pressUp, {position: this.props.position})
    this.props.click(this.props.position);
  };

  render() {
    return (
      <div className={this.buttonClasses}>
        <MuiThemeProvider>
          <RaisedButton name="button"
            className="RaisedButton" label={this.props.text}
            onMouseDown={()=>this.addAnimation(pressDown)}
            onMouseUp={()=>this.addAnimation(pressUp, {position: this.props.position})}
            onMouseLeave={()=>this.addAnimation(pressUp, {position: this.props.position})}
            onClick={this.handleClick} />
        </MuiThemeProvider>
      </div>
    );
  }
}

const GSAPMainButton = GSAP()(MainButton);
export default GSAPMainButton;


