import React, { Component } from 'react';
import './Content.scss';

import QuoteBox from './QuoteBox';

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import FloatingActionButton from 'material-ui/FloatingActionButton';
import EditorFormatQuote from 'material-ui/svg-icons/editor/format-quote';
import ContentAdd from 'material-ui/svg-icons/content/add';

import {TimelineMax} from 'gsap';
import GSAP from 'react-gsap-enhancer';

function contentAnim({target}) {
  return new TimelineMax({paused: true})
    .to(target, 1.5, {})
    .add('hidden')
    .to(target, 1, {opacity: 1}, 'hidden')
    .add('visible')
}

function randomKey(obj) {
  var keys = Object.keys(obj);
  return keys[keys.length * Math.random() << 0];
}

class Content extends Component {
  constructor(props) {
    super(props);

    this.state = {
      quote: '',
      isAnimating: false
    };

    this.quotes = [{date: Date.now(), quote: 'Actually?!'}];
  }

  componentWillMount() {
    window.database.ref('/quotes').once('value').then((snapshot) => {
      this.quotes = snapshot.val();
      console.log(this.quotes);
    })
  }

  componentDidMount() {
    this.contentAnimation = this.addAnimation(contentAnim);
    this.contentAnimation.tweenTo('visible');

    this.clearQuote(true);
  }

  update = (quote) => {
    this.setState({
      quote
    });
  };

  quoteAction = () => {
    this.setState({
      isAnimating: true
    });
    this.quoteBox.animateClear(this.clearQuote);
  };

  clearQuote = (isInit) => {
    var controller = this;
    function finish() {
      if (isInit !== true) {
        controller.quoteBox.animateShow(()=>{});
      }
    }

    if (this.props.parentPosition === 'left') {
      this.getQuote((quote) => {
        this.setState({quote, isAnimating: false});
        finish();
      });
    } else {
      if (isInit !== true) {
        this.saveQuote(this.state.quote, () => {
          this.setState({quote: '', isAnimating: false});
          finish();
        });
      }
    }
  };

  getQuote = (cb) => {
    if (Object.keys(this.quotes).length <= 0) {
      cb('No more quotes :(');
      return;
    }

    var key = randomKey(this.quotes);
    cb(this.quotes[key].quote);
    delete this.quotes[key];
  };

  saveQuote = (quote, cb) => {
    var key = window.database.ref().child('quotes').push().key;
    var updates = {};
    updates['/quotes/' + key] = {quote, date: Date.now()};
    window.database.ref().update(updates);

    console.log('saved '+quote);
    cb();
  };

  render() {

    return (
      <div className="Content">
        <div className="textFieldBox">
          { this.props.parentPosition === 'left' &&
          <MuiThemeProvider>
            <FloatingActionButton className="quoteButton" onClick={this.quoteAction}
                                  disabled={this.state.isAnimating} >
              <EditorFormatQuote />
            </FloatingActionButton>
          </MuiThemeProvider>
          }

          <QuoteBox position={this.props.parentPosition} onChange={this.update} value={this.state.quote}
                    ref={(quoteBox) => {this.quoteBox = quoteBox;}}
                    clear={this.clearQuote}
          />

          { this.props.parentPosition === 'right' &&
          <MuiThemeProvider>
            <FloatingActionButton className="addButton" onClick={this.quoteAction}
                                  disabled={this.state.isAnimating} >
              <ContentAdd />
            </FloatingActionButton>
          </MuiThemeProvider>
          }
        </div>
      </div>
    );
  }
}

const GSAPContent = GSAP()(Content);
export default GSAPContent;



