import React, { Component } from 'react';
import TextField from 'material-ui/TextField';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import {TimelineMax} from 'gsap';
import GSAP from 'react-gsap-enhancer';

import './QuoteBox.scss';

function textAnim({target, options}) {
  // Shitty!
  var container = target.find({id: 'quoteField'});
  var textarea = target.find({id: 'quoteField'})[0].children[0].children[1];

  if (options.type === 'clear') {
    return new TimelineMax()
      .set(container, {'overflow': 'hidden'})
      .to(textarea, 0.7, {delay: 0, 'top': textarea.style.height, ease: 'Power4.easeOut'})
      .call(options.callback);
  }
  else if (options.type === 'show') {
    return new TimelineMax()
      .to(textarea, 0.7, {delay: 0, 'top': '0px', ease: 'Power4.easeOut'})
      .set(container, {'overflow': 'visible'});
  }
}

class QuoteBox extends Component {
  constructor(props) {
    super(props);

    this.classes = "TextField " + props.position;
  }

  handleChange = (event) => {
    this.props.onChange(event.target.value);
  };

  killAnimation = (animation) => {
    if (animation !== undefined) {
      animation.kill();
    }
  };

  animateClear = (cb) => {
    this.killAnimation(this.clearAnimation);
    this.clearAnimation = this.addAnimation(textAnim, {type: 'clear', callback: cb});
  };

  animateShow = (cb) => {
    this.killAnimation(this.showAnimation);
    this.showAnimation = this.addAnimation(textAnim, {type: 'show', callback: cb});
  };


  render() {
    return (
      <div className="QuoteBox">
        <MuiThemeProvider>
          <TextField id="quoteField"
                     name="quoteField"
                     className={this.classes}

                     fullWidth={true}
                     multiLine={true}
                     rowsMax={13}

                     value={this.props.value}
                     onChange={this.handleChange}
                     maxLength="140"
          />
        </MuiThemeProvider>
      </div>
    );
  }
}

const GSAPQuoteBox = GSAP()(QuoteBox);
export default GSAPQuoteBox;


