import React, { Component } from 'react';
import './Title.scss';

import AtvImg from 'react-atv-img';
import K from './K_out.png';
import E from './E_out.png';
import N from './N_out.png';
import Q from './Q_out.png';
import U from './U_out.png';
import O from './O_out.png';
import T from './T_out.png';
import E2 from './E2_out.png';

class Title extends Component {
  constructor(props) {
    super(props);

    this.letters = [K,E,N,Q,U,O,T,E2];
  }

  handleClick = () => {
    this.props.click('center');
  };

  render() {
    var letterStyle = {width: '100%', height: '100%'};

    return (
      <div className="Title" onClick={this.handleClick}>
        {this.letters.map((letter, i) => {
          return (
            <div className="letterBox" key={i}>
              <AtvImg className="letterTitle"
                      layers={[letter]}
                      style={letterStyle}
              />
            </div>
          )
        })}
      </div>
    );
  }
}

export default Title;



