import React, { Component } from 'react';
import './Divider.scss';

import Content from './../Content/Content';
import MainButton from './MainButton';
import Title from './../Title/Title';

import {TweenMax} from 'gsap';
import GSAP from 'react-gsap-enhancer';

class Divider extends Component {
  constructor(props) {
    super(props);

    this.positions = {
      'left': '10vw',
      'center': '50vw',
      'right': '90vw'
    };
    this.position = null;
  }

  componentDidMount() {
    this.slide('center');
  }

  slide = (toPosition) => {
    this.position = toPosition;
    var delay = 0.8;
    if (toPosition === 'center') {delay=0;}

    this.addAnimation(({target}) => {
      return TweenMax.to(target, 1, {
        delay: delay,
        width: this.positions[toPosition],
        ease: 'Power2.easeInOut'
      });
    })
  };

  render() {
    return (
      <div className="Divider">
        <Title click={this.slide} />
        <div className="half leftHalf">
        </div>
        <div className="half rightHalf">
        </div>
        <div className="buttonBox">
          <MainButton position="left" text="Generate" click={this.slide} parentPosition={this.position} />
          <MainButton position="right" text="Contribute" click={this.slide} parentPosition={this.position} />
        </div>
        { this.position !== 'center' &&
        <Content parentPosition={this.position} />
        }
      </div>
    );
  }
}

const GSAPDivider = GSAP()(Divider);
export default GSAPDivider;



